import React, { useState } from 'react';
import { Link, Outlet } from 'react-router-dom';  // Link Outlet 
import { motion } from 'framer-motion';
import './css/Sidebar.css'; // Sidebar  

function Layout() {
    const [isOpen, setIsOpen] = useState(false);

    const toggleAccordion = () => {
        setIsOpen(!isOpen);
    };

    return (
        <motion.div
            className="layout"
            initial={{ y: -1000 }}
            animate={{ y: 0 }}
            transition={{
                type: "spring",
                duration: 1.2,
                bounce: 0.3, //   
            }}
        >
            {/*  */}
            <header className="header">
                <div className="logo">insH-MyLab</div>
                <nav>
                    <Link to="/">Home</Link>  {/* Link  */}
                    <a href="#">About</a>
                    <a href="#">Contact</a>
                </nav>
            </header>

            {/*  &   */}
            <div className="content">
                <aside className="sidebar">
                    <div className="accordion-item">
                        <button onClick={toggleAccordion} className="accordion-title">
                             API  1
                        </button>
                        <motion.div
                            style={{ overflow: 'hidden' }}
                            initial={{ height: 0, opacity: 0 }}
                            animate={{ height: isOpen ? 'auto' : 0, opacity: isOpen ? 1 : 0 }}
                            transition={{ duration: 0.8, ease: "easeInOut" }}
                        >
                            {isOpen && (
                                <ul>
                                    <li><Link to="/api1">API 1: User Data</Link></li>  {/* Link  */}
                                    <li><a href="#">API 2: Product List</a></li>
                                    <li><a href="#">API 3: Order Management</a></li>
                                </ul>
                            )}
                        </motion.div>
                    </div>
                </aside>

                {/* `Outlet`      */}
                <motion.div
                    className="main-content"
                    initial={{ y: -100, opacity: 0 }}  //    
                    animate={{ y: 0, opacity: 1 }}     //   
                    transition={{
                        type: "spring",  //   
                        duration: 1.5,   //  
                        bounce: 0.4,     //   
                    }}
                >
                    <Outlet />  {/*      */}
                </motion.div>
            </div>

            {/*  */}
            <footer className="footer">
                <p>&copy; 2024 My API Blog. All rights reserved.</p>
            </footer>
        </motion.div>
    );
}

export default Layout;
